
.auth__main {
    align-items: center;
    background-image: url(../../assets/layered-waves-haikei.svg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    margin: 0;
    height: 100vh;
    width: 100vw;
}

.auth__box-container {
    background-color: white; 
    box-shadow: 0px 3px blue;
    border-radius: 10px;
    padding: 20px;
    width: 250px;
}

.auth__title {
    color:darken($color: $primary, $amount: 20);
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 20px;
}

.auth__input {
    color: $dark-grey;
    border: 0px;
    border-bottom: 1px solid $light-grey;
    font-size: 16px;
    margin-bottom: 10px;
    height: 20px;
    width: 100%;
    transition: border-bottom 0.3s ease;


    &:focus{
        border-bottom: 1px solid $primary;
        outline: none;
    }
}

.auth__social-networks {
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 100%;
}

.auth__alert-error {
    background-color: red;
    border-radius: 5px;
    color: white;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    padding: 5px;
}